@keyframes zoom-out {
  from {
    transform: scale(1.2);
  }

  to {
    transform: scale(1);
  }
}

.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  img {
    position: absolute;
    width: 100%;
  }
}

.result__suitcase {
  width: 400px;
  height: 400px;
  left: 50vw;
  will-change: transform;
  transition: transform 1s 250ms cubic-bezier(0.15, 0.74, 0.23, 1);
  transform: translate(-200px, 100vh) scale(0) rotate(-10deg);
  z-index: 2;

  .screen.screen--show & {
    transform: translate(-200px, 29vh) scale(1) rotate(-10deg);
  }
}

.result__flamingo {
  width: 250px;
  left: 50vw;
  top: 100vh;
  opacity: 0;
  will-change: transform;
  transform-origin: 20% 60%;
  transform: translateX(-190px) rotate(-35deg);
  transition:
    top 1s 300ms cubic-bezier(0.15, 0.74, 0.23, 1),
    transform 0.8s 300ms cubic-bezier(0.15, 0.74, 0.23, 1),
    opacity 1s 300ms cubic-bezier(0.15, 0.74, 0.23, 1);
  animation: flamingo-wave 3s 1.6s ease-in-out infinite alternate;
  z-index: 3;

  .screen.screen--show & {
    top: 40vh;
    opacity: 1;
    transform: translateX(-190px) rotate(0);
  }
}

.result__leaf-blue-1 {
  left: 50vw;
  top: 30vh;
  opacity: 0;
  will-change: transform;
  transform-origin: 0 70%;
  animation: blue-leaf-right 1.6s 700ms cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

.result__leaf-blue-2 {
  left: 50vw;
  top: 40vh;
  opacity: 0;
  will-change: transform;
  transform-origin: 100% 80%;
  transform: translateX(-300px);
  animation: blue-leaf-left 1.6s 800ms cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

.result__leaf-green {
  left: 50vw;
  top: 43vh;
  opacity: 0;
  will-change: transform;
  transform-origin: 0 70%;
  animation: blue-leaf-right 1.6s 900ms cubic-bezier(0.45, 0.56, 0.45, 0.94) forwards;
  z-index: 1;
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;
  opacity: 0;
  transition: opacity 250ms ease-out 250ms;

  .screen--result.screen--show & {
    opacity: 1;
  }

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;
    transform: translateX(6rem) rotate(105deg);
    transition: transform 250ms linear 250ms;

    .screen--result.screen--show & {
      transform: translateX(0) rotate(0);
    }

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;
    animation: zoom-out 500ms ease-out both;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }
}

.result--negative {
  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }
}
