@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes pop {
  from {
    transform: translateY(125%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes drop {
  from {
    transform: translateY(-125%);
  }
  to {
    transform: translateY(0);
  }
}
.anim-popping {
  display: inline-flex;
  overflow: hidden;
  padding-top: 0.2em;
  margin-top: -0.2em;
}

.anim-popping__letter {
  animation: pop both;
}
.anim-popping__letter--reverse {
  animation: drop both;
}

@keyframes flamingo-wave {
  from {
    transform: translate(-190px, 0) rotate(0);
  }
  to {
    transform: translate(-190px, 45px) rotate(-10deg);
  }
}
@keyframes blue-leaf-left {
  from {
    transform: translateX(-300px) rotate(75deg);
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  20% {
    transform: translateX(-300px) rotate(-15deg);
  }
  40% {
    transform: translateX(-300px) rotate(10deg);
  }
  80% {
    transform: translateX(-300px) rotate(-8deg);
  }
  to {
    transform: translateX(-300px) rotate(-4deg);
    opacity: 1;
  }
}
@keyframes blue-leaf-right {
  from {
    transform: rotate(-60deg);
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  20% {
    transform: rotate(20deg);
  }
  40% {
    transform: rotate(-10deg);
  }
  80% {
    transform: rotate(10deg);
  }
  to {
    transform: rotate(5deg);
    opacity: 1;
  }
}
@keyframes slide {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
.prizes--bg .animate-bg {
  transform: scaleY(1);
}

.prizes--bg .active {
  opacity: 0;
}

.prizes--bg .screen--intro {
  z-index: -1;
}

.animate-bg {
  position: absolute;
  z-index: 1;
  bottom: -100%;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: #5f458c;
  transition: bottom 0.4s cubic-bezier(0.14, 0.57, 0.32, 0.9);
}

.prizes--active {
  transition: all 0.5s ease;
  opacity: 1 !important;
}

.js-menu-transition {
  opacity: 1 !important;
}

.animation-screen {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.menu-opened .page-header__nav {
  transform: translate(0, 0);
  opacity: 1;
}

@media (min-width: 768px) {
  .add-transition .page-header__nav {
    transform: translate(0, 0);
    opacity: 1;
  }
}

.add-transition .page-header__logo {
  transform: translate(0, 0);
  opacity: 1;
}

.add-transition .screen__footer {
  transform: translate(0, 0);
  opacity: 1;
}

.screen--prizes.screen--hidden.active {
  display: block;
  position: absolute;
}
.screen--prizes.screen--hidden.active ~ .animate-bg {
  bottom: 0;
}

.screen--prizes.active:not(.screen--hidden) ~ .animate-bg {
  display: none;
}

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 1.5rem 2.9rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3rem;
  color: #5f458c;
  letter-spacing: -0.04em;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
  z-index: -1;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.7rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.9rem 0;
  width: 100%;
  max-height: calc(100vh - 33.9rem);
  max-width: 79.8rem;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    padding: 0 59px 0;
    max-height: calc(100vh - 339px);
    max-width: 798px;
  }
}
@media (orientation: portrait) {
  .chat__body {
    padding: 0 0 0;
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
