    padding: 0 0.4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
.disclaimer li::before {
  content: "/";
  position: absolute;
  top: 50%;
