    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__field {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field {
    margin-right: 1rem;
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
    height: 4rem;
    font-size: 1.6rem;
  }
}

.form__button {
  padding: 1.5rem 4.55rem;
}
@media (orientation: portrait) {
